radient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));\n  }\n  .swiper-slide-shadow-top {\n    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));\n  }\n  .swiper-slide-shadow-bottom {\n    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));\n  }\n}\n.swiper-lazy-preloader {\n  width: 42px;\n  height: 42px;\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  margin-left: -21px;\n  margin-top: -21px;\n  z-index: 10;\n  transform-origin: 50%;\n  box-sizing: border-box;\n  border: 4px solid var(--swiper-preloader-color, var(--swiper-theme-color));\n  border-radius: 50%;\n  border-top-color: transparent;\n}\n.swiper:not(.swiper-watch-progress),\n.swiper-watch-progress .swiper-slide-visible {\n  .swiper-lazy-preloader {\n    animation: swiper-preloader-spin 1s infinite linear;\n  }\n}\n.swiper-lazy-preloader-white {\n  --swiper-preloader-color: #fff;\n}\n.swiper-lazy-preloader-black {\n  --swiper-preloader-color: #000;\n}\n@keyframes swiper-preloader-spin {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n/* Slide styles end */\n\r\n\r\n\r\n","@import '../swiper-vars.scss';\n@at-root {\n  :root {\n    /*\n  --swiper-pagination-color: var(--swiper-theme-color);\n  --swiper-pagination-left: auto;\n  --swiper-pagination-right: 8px;\n  --swiper-pagination-bottom: 8px;\n  --swiper-pagination-top: auto;\n  --swiper-pagination-fraction-color: inherit;\n  --swiper-pagination-progressbar-bg-color: rgba(0,0,0,0.25);\n  --swiper-pagination-progressbar-size: 4px;\n  --swiper-pagination-bullet-size: 8px;\n  --swiper-pagination-bullet-width: 8px;\n  --swiper-pagination-bullet-height: 8px;\n  --swiper-pagination-bullet-border-radius: 50%;\n  --swiper-pagination-bullet-inactive-color: #000;\n  --swiper-pagination-bullet-inactive-opacity: 0.2;\n  --swiper-pagination-bullet-opacity: 1;\n  --swiper-pagination-bullet-horizontal-gap: 4px;\n  --swiper-pagination-bullet-vertical-gap: 6px;\n  */\n  }\n}\n.swiper-pagination {\n  position: absolute;\n  text-align: center;\n  transition: 300ms opacity;\n  transform: translate3d(0, 0, 0);\n  z-index: 10;\n  &.swiper-pagination-hidden {\n    opacity: 0;\n  }\n  .swiper-pagination-disabled > &,\n  &.swiper-pagination-disabled {\n    display: none !important;\n  }\n}\n/* Common Styles */\n.swiper-pagination-fraction,\n.swiper-pagination-custom,\n.swiper-horizontal > .swiper-pagination-bullets,\n.swiper-pagination-bullets.swiper-pagination-horizontal {\n  bottom: var(--swiper-pagination-bottom, 8px);\n  top: var(--swiper-pagination-top, auto);\n  left: 0;\n  width: 100%;\n}\n/* Bullets */\n.swiper-pagination-bullets-dynamic {\n  overflow: hidden;\n  font-size: 0;\n  .swiper-pagination-bullet {\n    transform: scale(0.33);\n    position: relative;\n  }\n  .swiper-pagination-bullet-active {\n    transform: scale(1);\n  }\n  .swiper-pagination-bullet-active-main {\n    transform: scale(1);\n  }\n  .swiper-pagination-bullet-active-prev {\n    transform: scale(0.66);\n  }\n  .swiper-pagination-bullet-active-prev-prev {\n    transform: scale(0.33);\n  }\n  .swiper-pagination-bullet-active-next {\n    transform: scale(0.66);\n  }\n  .swiper-pagination-bullet-active-next-next {\n    transform: scale(0.33);\n  }\n}\n.swiper-pagination-bullet {\n  width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 8px));\n  height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 8px));\n  display: inline-block;\n  border-radius: var(--swiper-pagination-bullet-border-radius, 50%);\n  background: var(--swiper-pagination-bullet-inactive-color, #000);\n  opacity: var(--swiper-pagination-bullet-inactive-opacity, 0.2);\n  @at-root button#{&} {\n    border: none;\n    margin: 0;\n    padding: 0;\n    box-shadow: none;\n    appearance: none;\n  }\n  .swiper-pagination-clickable & {\n    cursor: pointer;\n  }\n\n  &:only-child {\n    display: none !important;\n  }\n}\n.swiper-pagination-bullet-active {\n  opacity: var(--swiper-pagination-bullet-opacity, 1);\n  background: var(--swiper-pagination-color, var(--swiper-theme-color));\n}\n\n.swiper-vertical > .swiper-pagination-bullets,\n.swiper-pagination-vertical.swiper-pagination-bullets {\n  right: var(--swiper-pagination-right, 8px);\n  left: var(--swiper-pagination-left, auto);\n  top: 50%;\n  transform: translate3d(0px, -50%, 0);\n  .swiper-pagination-bullet {\n    margin: var(--swiper-pagination-bullet-vertical-gap, 6px) 0;\n    display: block;\n  }\n  &.swiper-pagination-bullets-dynamic {\n    top: 50%;\n    transform: translateY(-50%);\n    width: 8px;\n    .swiper-pagination-bullet {\n      display: inline-block;\n      transition: 200ms transform, 200ms top;\n    }\n  }\n}\n.swiper-horizontal > .swiper-pagination-bullets,\n.swiper-pagination-horizontal.swiper-pagination-bullets {\n  .swiper-pagination-bullet {\n    margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 4px);\n  }\n  &.swiper-pagination-bullets-dynamic {\n    left: 50%;\n    transform: translateX(-50%);\n    white-space: nowrap;\n    .swiper-pagination-bullet {\n      transition: 200ms transform, 200ms left;\n    }\n  }\n}\n.swiper-horizontal.swiper-rtl > .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {\n  transition: 200ms transform, 200ms right;\n}\n/* Fraction */\n.swiper-pagination-fraction {\n  color: var(--swiper-pagination-fraction-color, inherit);\n}\n/* Progress */\n.swiper-pagination-progressbar {\n  background: var(--swiper-pagination-progressbar-bg-color, rgba(0, 0, 0, 0.25));\n  position: absolute;\n  .swiper-pagination-progressbar-fill {\n    background: var(--swiper-pagination-color, var(--swiper-theme-color));\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    transform: scale(0);\n    transform-origin: left top;\n  }\n  .swiper-rtl & .swiper-pagination-progressbar-fill {\n    transform-origin: right top;\n  }\n  .swiper-horizontal > &,\n  &.swiper-pagination-horizontal,\n  .swiper-vertical > &.swiper-pagination-progressbar-opposite,\n  &.swiper-pagination-vertical.swiper-pagination-progressbar-opposite {\n    width: 100%;\n    height: var(--swiper-pagination-progressbar-size, 4px);\n    left: 0;\n    top: 0;\n  }\n  .swiper-vertical > &,\n  &.swiper-pagination-vertical,\n  .swiper-horizontal > &.swiper-pagination-progressbar-opposite,\n  &.swiper-pagination-horizontal.swiper-pagination-progressbar-opposite {\n    width: var(--swiper-pagination-progressbar-size, 4px);\n    height: 100%;\n    left: 0;\n    top: 0;\n  }\n}\n.swiper-pagination-lock {\n  display: none;\n}\n","@import \"bootstrap/dist/css/bootstrap.min.css\";\n@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap');\n\n\n.page {\n  &__body {\n    width: 100%;\n    max-width: 500px;\n    min-height: 100vh;\n    margin: 0 auto;\n    display: flex;\n    flex-direction: column;\n    font-family: 'Open Sans', sans-serif;\n    color: #000;\n    font-size: 14px;\n    line-height: 20px;\n  }\n\n  .hidden {\n    display: none;\n  }\n\n  .no-margin-bottom {\n    margin-bottom: 0;\n  }\n\n  .no-margin-top {\n    margin-top: 0;\n  }\n}\n\nh1, h2, h3, h4, h5, h6{\n  font-family: 'Montserrat', sans-serif;\n  font-weight: 600;\n}\n\ntd, th{\n  text-align: center;\n}\n\n// \n.button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: relative;\n  overflow: hidden;\n  margin-top: 20px;\n  padding: 0 40px;\n  text-decoration: none;\n  min-height: 55px;\n  position: relative;\n  background-color: $green;\n  color: #fff;\n  border: none;\n  border-radius: 40px;\n  font-size: 18px;\n\n  &::after {\n    background: linear-gradient(90deg,\n        hsla(0, 0%, 100%, 0.1) 10%,\n        hsla(0, 0%, 100%, 0.2) 20%,\n        hsla(0, 0%, 100%, 0.6));\n    width: 20px;\n    content: \"\";\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    height: 100%;\n    transform: skewX(-45deg);\n    transition: all 0.6s ease;\n    left: -20%;\n    animation-name: blick;\n    animation-duration: 3s;\n    animation-iteration-count: infinite;\n  }\n\n  @keyframes blick {\n    0% {\n      left: -20%;\n    }\n\n    100% {\n      left: 110%;\n    }\n  }\n}\n\n// \n.main-title {\n  margin: 0;\n  font-size: $font-size-main-title;\n  line-height: $line-height-2;\n  font-weight: 600;\n}\n\n.title {\n  margin: 0;\n  font-size: $font-size-title;\n  line-height: $line-height-2;\n  font-weight: 600;\n}\n\n.sub-title {\n  margin: 0;\n  font-size: $font-size-sub-title;\n  line-height: $line-height;\n  font-weight: 600;\n}\n\n.text {\n  margin: 0;\n  font-size: $font-size-text;\n  line-height: $line-height;\n}","//  \n$font-size-text: 16px;\n$font-size-title: 24px;\n$font-size-sub-title: 20px;\n$font-size-main-title: 28px;\n\n//  \n$line-height: 1.5;\n$line-height-2: 1.35;\n\n// \n$border-radius-1: 5px;\n$border-radius-2: 10px;\n$border-radius-3: 50%;\n\n// \n$box-shadow-1: 0 2px 5px rgba(0, 0, 0, 0.25);\n$box-shadow-2: 1.9px 6.7px 7px 0 rgba(0, 0, 0, 0.1);\n\n// \n$green: #3db92c;\n$light-green: #eff8f0;\n$dark-green: #126c84;\n$white: #fff;\n$black: #000;\n$red: red;\n$light-gray: #f7f7f7;\n$dark-gray: #dbdada;\n$dark-gray-2: #9e9e9e;\n$dark-blue: #0070ba;\n",".first-screen {\n  &__container {\n    display: flex;\n    flex-direction: column;\n    flex-grow: 1;\n    height: 100%;\n    padding: 15px;\n  }\n\n  &__title {\n    margin: 0;\n    font-size: 28px;\n    line-height: 1.2;\n    margin-bottom: 16px;\n  }\n\n  &__img {\n    display: block;\n    width: 100%;\n    height: auto;\n    object-fit: cover;\n    margin-bottom: 10px;\n  }\n\n  &__container-data {\n    display: block;\n    font-size: 12px;\n    line-height: 1.3;\n    margin-bottom: 10px;\n  }\n\n  &__doctor-figure {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n  }\n\n 